}/*Versión escritorio*/.info__container-desk {
  width: auto;
  height: auto;
  margin: 10px;
  overflow: hidden;
  position: relative;
  text-align: center;
  display: none;
}

.image-desk {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.view .image-desk {
  transition: all 0.3s ease-in-out;
}

.view:hover .image-desk {
  transform: translateX(100%);
  transition-delay: 0.1s;
}

.image__subtitle-desk {
  text-transform: uppercase;
  text-align: center;
  position: relative;
  font-size: 17px;
  padding: 10px;
  margin: 20px 0 0 0;
  background: rgba(255, 255, 255, 0.7);
  color: #000;
  box-shadow: 0px 1px 3px rgba(159, 141, 140, 0.5);
}

.info__image-desk {
  width: 100%;
  height: 200px;
  position: absolute;
  overflow: hidden;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.view .info__image-desk {
  background-color: #ffdc60;
  transform: translateX(-100%);
  opacity: 1;
  transition: all 0.4s ease-in-out;
}

.view:hover .info__image-desk {
  transform: translateX(0);
}

.view .image__text-desk {
  height: 75%;
  display: inline-block;
  padding: 7px 14px;
  background: rgba(255, 255, 255, 0.7);
  color: #000;
  box-shadow: 0px 1px 3px rgba(159, 141, 140, 0.5);
  text-transform: uppercase;
  transition: all 0.2s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.view:hover .view .image__text-desk {
  opacity: 1;
  transition-delay: 0.5s;
}

